<template>
  <div class="search">
    <van-search
      v-model="value"
      show-action
      placeholder="请输入搜索关键词"
    >
      <template #action>
        <div @click="onSearch">搜索</div>
      </template>
      <template #left>
        <div @click="back">
          <van-icon size=20 name="arrow-left" />
        </div>
      </template>
    </van-search>

    <van-empty v-show="isSearchNull" image="search" description="未找到你想要的商品" />

    <!-- 产品列表 -->
    <div class="product-list-box" v-show="!isSearchNull && searchData.length != 0">    
      <div class="product-item" v-for="(item,index) in searchData" :key="index" @click="viewProductDetail(item.pid)">
        <div class="product-img-box">
          <img class="auto-img" :src="item.smallImg">
        </div>
        <div class="product-info clearfix">
          <div class="product-info-left fl">
            <div class="product-name">{{item.name}}</div>
            <div class="product-enname">{{item.enname}}</div>
          </div>
          <div class="product-info-right fr">
            <div class="product-price">{{item.price}}</div>
          </div>
        </div>
      </div>

    </div>

  </div>
</template>

<script>
  import '../assets/less/search.less'
  export default {
    data(){
      return{
        value: '',
        searchData: [],
        isSearchNull: false,
      }
    },
    methods:{
      onSearch(){
        if(this.value == ''){
          this.$toast('请输入关键字')
          return;
        }
        this.getSearchData();
      },
      back(){
        this.$router.go(-1);
      },
      getSearchData(){
        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration: 0,
          loadingType: "spinner",
        });

        this.axios({
          method: "GET",
          url: "/search",
          params: {
            appkey: this.appkey,
            name: this.value
          },
        }).then((result) => {
            this.$toast.clear();
            if (result.data.code == "Q001") {
              this.searchData = result.data.result;
              if(result.data.result.length == 0){
                this.isSearchNull = true;
              }else{
                this.isSearchNull = false;
              }
            }
            // console.log("result ==>", result);
          })
          .catch((err) => {
            this.$toast.clear();
              // console.log("err ==>", err);
          });

      },
      // 查看产品详情
      viewProductDetail(pid){
        this.$router.push({
          name: 'Detail',
          query: {pid}
        });
      }
    },
  }
</script>

